html, body, .fillArea {
  margin: 0;
  height: 100%;
  width: 100%;
  background-color: rgb(249, 249, 249);
}

.mainContainer {
  display: grid;
  grid-template-rows: 80px auto;
  grid-row-gap: 30px;
  grid-template-areas:
    "header"
    "content";
  justify-items: center;
}



