.thumbnailContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  max-height: 1000px;
  margin-bottom: 140px;
}

.thumbnailContainer_content {
  display: grid;
  /* TODO CSS */
  /* grid-template-columns: auto auto auto auto; */
  grid-template-columns: auto auto;
  grid-gap: 5px;
  overflow: hidden;
  height: 95%;
  width: 100%;
}

.thumbnailContainer_content:hover {
  overflow-y: auto;
}

@media (max-width: 500px) {
  .thumbnailContainer {
    max-height: 700px;
    margin-bottom: 50px;
  }
  .thumbnailContainer_content {
    grid-template-columns: auto;
    overflow-y: auto;
  }
}



